import "./App.css";
import Banana from "./images/banana.jfif";
import Orange from "./images/orange.jfif";
import Pineapple from "./images/pineapple.jfif";
import Blueberry from "./images/blueberry.jfif";
import Strawberry from "./images/strawberry.jfif";
import Cherries from "./images/cherries.jfif";
import React, { Component } from "react";
import Home from "./components/Home";
import CartProduct from "./components/CartProduct";

class App extends Component {
  state = {
    cartList: [],
    productsPage: true,
    products: [
      {
        name: "Banana",
        cost: 5.9,
        image: <img src={Banana} alt="Banana" />,
      },
      {
        name: "Orange",
        cost: 4.9,
        image: <img src={Orange} alt="Orange" />,
      },
      {
        name: "Pineapple",
        cost: 36.9,
        image: <img src={Pineapple} alt="Pineapple" />,
      },
      {
        name: "Blueberry",
        cost: 22.9,
        image: <img src={Blueberry} alt="Blueberry" />,
      },
      {
        name: "Strawberry",
        cost: 29.9,
        image: <img src={Strawberry} alt="Strawberry" />,
      },
      {
        name: "Cherries",
        cost: 24.9,
        image: <img src={Cherries} alt="Cherries" />,
      },
    ],
  };

  addToCart = (n, c, i) => {
    this.setState({
      cartList: [{ name: n, cost: c, image: i }, ...this.state.cartList],
    });
  };

  deleteFromCart = (i) => {
    let tempCartList = this.state.cartList.filter(
      (element, index) => index !== i
    );
    this.setState({ cartList: tempCartList });
  };

  totalPrice = () => {
    let totalPrice = 0;
    this.state.cartList.forEach((element) => {
      totalPrice += element.cost;
    });
    if (totalPrice === 0) return "your cart i empty";
    else return totalPrice + "â‚ª";
  };

  hidden() {
    if (this.state.productsPage) {
      return (
        <div>
          <div
            style={{
              width: "1000px",
              display: "flex",
              flexWrap: "wrap",
              justifyContent: "space-around",
              alignContent: "center",
            }}
          >
            {this.state.products.map((element, i) => {
              return (
                <Home
                  add={this.addToCart}
                  name={element.name}
                  cost={element.cost}
                  image={element.image}
                />
              );
            })}
          </div>
        </div>
      );
    } else {
      return (
        <div>
          <div
            style={{
              width: "1000px",
              display: "flex",
              flexWrap: "wrap",
              justifyContent: "space-around",
              alignContent: "center",
              alignItems: "center",
            }}
          >
            {this.state.cartList.map((element, i) => {
              return (
                <CartProduct
                  dell={this.deleteFromCart}
                  name={element.name}
                  cost={element.cost}
                  image={element.image}
                  index={i}
                />
              );
            })}
          </div>
          <div>
            <p>
              <h3>Total Price:{this.totalPrice()}</h3>
            </p>
          </div>
        </div>
      );
    }
  }

  render() {
    return (
      <div>
        <div className="homeBody">
          <div className="headerPage">
            <h1>Shopping Cart</h1>
            <button
              onClick={() => {
                this.setState({ productsPage: true });
              }}
              className="homeButton"
            >
              <ion-icon name="home-outline"></ion-icon>
            </button>
            <button
              onClick={() => {
                this.setState({ productsPage: false });
              }}
              className="cartButton"
            >
              <p>
                <ion-icon name="cart-outline"></ion-icon>
                {this.state.cartList.length}
              </p>
            </button>
          </div>
          {this.hidden()}
        </div>
      </div>
    );
  }
}

export default App;
